import React from 'react'
import './schoolid.scss'
import schoolid from '../../../assets/images/personal/SchoolID.jpg'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import HighlightOffRoundedIcon from '@mui/icons-material/HighlightOffRounded';
import { useNavigate } from 'react-router-dom';

const SchoolID = () => {

let history = useNavigate();

  return (
    <div className='schoolid'>
        <div className="container">
            <div className="left">
                <button onClick={() => history('/prelims/personal/MDR')} className='button'><ArrowBackIosIcon/></button>
            </div>
            <div className="center">
                <img src={schoolid} alt="SchoolID" className='image' width="100%" height={660}/>
            </div>
            <div className="right">
                <button onClick={() => history('/prelims/personal')} className='close'><HighlightOffRoundedIcon/></button>
                <button onClick={() => history('/prelims/personal/philhealthID')} className='button'><ArrowForwardIosIcon/></button>
            </div>
        </div>
    </div>
  )
}

export default SchoolID